body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

g.features {
  stroke: #d8d8d8;
  stroke-width: 0.5;
}
g.features path:hover {
  opacity: 0.5;
}

.tooltip {
  font-weight: bold;
  padding: 0.5rem;
  border: 1px solid silver;
  color: #222;
  background: #fff;
  border-radius: 5px;
  box-shadow: 0px 0px 5px 0px #a6a6a6;
  opacity: 0.9;
  position: absolute;
  font-size: 1.5rem;
}

#map {
	margin-top: 2%;
}

.hidden {
  display: none;
}

#main {
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}

#details, #initial {
  margin-top: 2rem;
}

#welcome {
	margin-right: 5%;
	margin-top: 5%;
	display : -webkit-flex;
	display : flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-content: space-around;
	        align-content: space-around;
	height: 100%;
	width: 100%;
}

#raleigh {
	font-size: 4em;
	margin-left: 25%;
}

#about {
	margin-top: 1%;
	font-weight: bold;
}

p {
	padding: 10px;
}

#instructions {
	font-style: oblique;
}
#head {
	height: 100%;
	width: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-align-content: space-between;
	        align-content: space-between;
}

table, th, td {
	border: 1px solid black;
}

#tabular {
	height: 60%;
}

#tab {
	height: 100%;
	width: 60%;
}


